.search {
	position: relative;
	left: -120px;

	&-input {
		border: 1px solid rgba(0, 0, 0, 0.1);
		padding: 12px 20px;
		width: 400px;
		border-radius: 10px;
		font-size: 16px;
		padding-left: 42px;
	
		&:focus {
			border: 1px solid rgba(0, 0, 0, 0.2);
	
		}
	}
	
	&-icon {
		width: 22px;
		height: 22px;
		opacity: 0.3;
		position: absolute;
		top: 12px;
		left: 14px;
		object-fit: cover;
	}
	
	&-close {
		width: 18px;
		height: 18px;
		position: absolute;
		top: 15px;
		right: 20px;
		opacity: 0.3;
		object-fit: cover;
		cursor: pointer;
	
		&:hover {
			opacity: 0.8;
		}
	}
}

